<template>
	<div class="notice" style="background: #f5f5f5;">
		<div class="row">
			<div class="col-lg-2 col-md-2">
				<leftmenu></leftmenu>
			</div>
			<div class="col-lg-10 col-md-10">
				<div class="row">
					<div class="col-lg-12 col-md-12">
						<rightheader></rightheader>
					</div>
				</div>
				<div class="row">
					<div class="col-lg-12 col-md-12">
						<div class="row" style="margin: 15px;background: #fff;height: 735px;overflow-y: scroll;margin-bottom: 0;">
							<div class="col-lg-12 col-md-12" style="padding: 30px 15px;">
								<div class="row">
									<div class="col-lg-12 col-md-12">
										<button type="button" class="pull-left btn btn-default" style="margin-right: 15px;" v-on:click="showCreateModal()">创建公告</button>
										<button type="button" class="pull-left btn btn-default" style="margin-right: 15px;">创建模块</button>
									</div>
								</div>
								<div class="row" style="margin-top: 30px;">
									<div class="col-lg-12 col-md-12">
										<ul class="pull-left nave" style="margin-bottom: 0;margin-left: 20px;padding-left: 0;">
											<a href="javascript:void(0)"><li class="pull-left active">首页</li></a>
											<a href="javascript:void(0)"><li class="pull-left">帮助中心</li></a>
											<a href="javascript:void(0)"><li class="pull-left">阿里巴巴代付页</li></a>
											<a href="javascript:void(0)"><li class="pull-left">淘宝天猫代付页</li></a>
											<a href="javascript:void(0)"><li class="pull-left">微信充值页</li></a>
											<a href="javascript:void(0)"><li class="pull-left">支付宝充值页</li></a>
										</ul>
									</div>
								</div>
								<div class="row">
									<div class="col-lg-12 col-md-12">
										<table class="table table-hover" style="width: 100%;table-layout: fixed;">
											<thead>
												<tr>
													<th style="border-top-left-radius: 5px;width: 10%;">模块名称</th>
													<th style="width: 20%;">公告名称</th>
													<th style="width: 30%;">公告内容</th>
													<th style="width: 25%;">公告链接</th>
													<th style="border-top-right-radius: 5px;width: 15%;">操作</th>
												</tr>
											</thead>
											<tbody>
												<tr v-for="item in noticeList">
													<td style="line-height: 40px;">{{item.model_name}}</td>
													<td style="line-height: 40px;">{{item.announcementTopic}}</td>
													<td style="line-height: 40px;">{{item.content}}</td>
													<td style="line-height: 40px;">{{item.link}}</td>
													<td>
														<a href="javascript:void(0)" v-on:click="editNotice(item.announcementID)"><img src="../../assets/images/administrator/bianji.png" style="width: 28px;margin-top: 6px;margin-right: 15px;"/></a>
														<a href="javascript:void(0)" v-on:click="checkNotice(item.announcementID)"><img src="../../assets/images/administrator/check.png" style="width: 28px;margin-top: 6px;margin-right: 15px;"/></a>
														<a href="javascript:void(0)" v-on:click="deleteNotice(item.announcementID)"><img src="../../assets/images/administrator/delete.png" style="width: 28px;margin-top: 6px;"/></a>
													</td>
												</tr>
											</tbody>
										</table>
									</div>
								</div>
								<div class="row nomargin">
									<div class="col-lg-12 col-md-12 paginate">
										<paginate :page-count="totalPage" :click-handler="currentPageInfo" :prev-text="'<'" :next-text="'>'" :container-class="'pull-right'"></paginate>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="row">
					<div class="col-lg-12 col-md-12">
						<!-- 创建公告模态框（Modal） -->
						<div class="modal fade" id="noticeModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
						    <div class="modal-dialog" style="width: 90%;">
						        <div class="modal-content">
						            <div class="modal-header" style="background: #33b8ff;padding: 10px 15px;border-top-left-radius: 5px;border-top-right-radius: 5px;">
						                <button type="button" class="close" data-dismiss="modal" aria-hidden="true" style="color: #fff;font-size: 14px;opacity: 1;">&times;</button>
				                		<h4 class="modal-title pull-left" id="myModalLabel" style="color: #fff;font-size: 14px;"><img src="../../assets/images/shensuicon.png" class="pull-left" style="width: 15px;margin-top: 3px;margin-right: 5px;"/>创建公告</h4>
						            </div>
						            <div class="modal-body" style="padding-bottom: 50px;">
						            	<div class="row">
						            		<div class="col-lg-10 col-md-10 col-lg-offset-1 col-md-offset-1">
						            			<div class="row" style="margin-top: 15px;" v-if="model_name == '帮助中心'">
								            		<div class="col-lg-12 col-md-12">
								            			<label class="pull-left" style="line-height: 34px;">模块名称：</label>
								            			<select name="" class="pull-left form-control" style="width: 30%;">
								            				<option value=""></option>
								            			</select>
								            		</div>
								            	</div>
								            	<div class="row" style="margin-top: 15px;">
								            		<div class="col-lg-12 col-md-12">
								            			<label class="pull-left" style="line-height: 34px;">公告名称：</label>
								            			<input type="text" class="pull-left form-control" name="" id="" value="" v-model="announcementTopic" style="width: 50%;"/>
								            		</div>
								            	</div>
								            	<div class="row" style="margin-top: 15px;">
								            		<div class="col-lg-12 col-md-12">
								            			<label class="pull-left" style="line-height: 34px;">公告链接：</label>
								            			<input type="text" class="pull-left form-control" name="" id="" value="" v-model="links" style="width: 50%;"/>
								            		</div>
								            	</div>
								            	<div class="row" style="margin-top: 15px;">
								            		<div class="col-lg-12 col-md-12">
								            			<label class="pull-left" style="line-height: 34px;">公告内容：</label>
								            		</div>
								            	</div>
								            	<div class="row" style="margin-top: 15px;">
								            		<div class="col-lg-12 col-md-12">
								            			<editor id='tinymce' v-model='tinymceHtml' :init='init'></editor>
								            		</div>
								            	</div>
								            	<div class="row" id="noticeBtnRow" style="margin-top: 30px;">
								            		<div class="col-lg-6 col-md-6">
								            			<button type="button" class="pull-right btn btn-default" style="margin-right: 15px;" v-on:click="submitNotice()">保存</button>
								            		</div>
								            		<div class="col-lg-6 col-md-6">
								            			<button type="button" class="btn btn-default pull-left" data-dismiss="modal" style="margin-left: 15px;">取消</button>
								            		</div>
								            	</div>
						            		</div>
						            	</div>
						            	
						            </div>
						            <!--<div class="modal-footer">
						                <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
						                <button type="button" class="btn btn-primary">提交更改</button>
						            </div>-->
						        </div><!-- /.modal-content -->
						    </div><!-- /.modal -->
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import leftmenu from './menu.vue'
	import rightheader from './rightheader.vue'
	import {getService} from '../../assets/js/configs.js'
	import tinymce from 'tinymce/tinymce'
	import 'tinymce/themes/modern/theme'
	import Editor from '@tinymce/tinymce-vue'
	import 'tinymce/plugins/image'
	import 'tinymce/plugins/link'
	import 'tinymce/plugins/code'
	import 'tinymce/plugins/table'
	import 'tinymce/plugins/lists'
	import 'tinymce/plugins/contextmenu'
	import 'tinymce/plugins/wordcount'
	import 'tinymce/plugins/colorpicker'
	import 'tinymce/plugins/textcolor'
	export default {
		name:'notice',
		data(){
			return {
				model_name:'首页',
				links:'',
				announcementTopic:'',
				announcementID:'',
				noticeList:[],
				currentPage:1,
			    totalPage:1,
			    pageCount:10,
			    flag:true,	//模态框保存按钮标志位，true为创建公告，false为编辑公告
			    tinymceHtml: '',
			      init: {
			        language_url: '/static/tinymce/zh_CN.js',
//			        language: 'zh_CN',
			        skin_url: '/static/tinymce/skins/lightgray',
			        height: 300,
			        plugins: 'link lists image code table colorpicker textcolor wordcount contextmenu',
			        toolbar:
			          'bold italic underline strikethrough | fontsizeselect | forecolor backcolor | alignleft aligncenter alignright alignjustify | bullist numlist | outdent indent blockquote | undo redo | link unlink image code | removeformat',
			        branding: false
			      }
			}
		},
		components:{
			leftmenu,
			rightheader,
			Editor
		},
		mounted:function () {
			this.getNotice(this.currentPage);
			tinymce.init({});
		},
		methods:{
			getNotice:function (num) {
				var param = {
					model_name:this.model_name,
					link:this.links,
					announcementTopic:this.announcementTopic,
					announcementID:this.announcementID,
					page:num,
					unit:this.pageCount
				}
				$.ajax({
		  			type:'post',
		  			url:getService()+"/findAnnouncement",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	this.noticeList = res.data;
				    }.bind(this)
		  		});
			},
			currentPageInfo:function (pageNum) {
				this.currentPage = pageNum;
				this.getNotice(this.currentPage);
			},
			showCreateModal:function () {
				this.flag = true;
				$('#noticeBtnRow').show();
				$('#noticeModal').modal('show');
			},
			submitNotice:function () {
				var param = {
					model_name:this.model_name,
					link:this.links,
					announcementTopic:this.announcementTopic,
					announcementID:this.announcementID,
					content:this.tinymceHtml
				}
				//根据flag判断点击按钮是为了创建公告还是更新公告
				if (this.flag) {
					$.ajax({
			  			type:'post',
			  			url:getService()+"/addAnnouncement",
			  			dataType: "json",
			  			data:JSON.stringify(param),
			  			async:true,
						headers: {'Content-Type': 'application/json'},
			  			xhrFields: {
					        withCredentials: true
					    },
					    crossDomain: true,
					    success:function (res) {
					    	if (res.error_code == '0') {
					    		window.location.reload();
					    	}
					    }.bind(this)
			  		});
				} else{
					$.ajax({
			  			type:'post',
			  			url:getService()+"/updateAnnouncement",
			  			dataType: "json",
			  			data:JSON.stringify(param),
			  			async:true,
						headers: {'Content-Type': 'application/json'},
			  			xhrFields: {
					        withCredentials: true
					    },
					    crossDomain: true,
					    success:function (res) {
					    	if (res.error_code == '0') {
					    		window.location.reload();
					    	}
					    }.bind(this)
			  		});
				}
				
			},
			checkNotice:function (num) {
				var param = {
					announcementID:num
				}
				$.ajax({
		  			type:'post',
		  			url:getService()+"/findAnnouncement",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == '0') {
				    		this.announcementTopic = res.data[0].announcementTopic;
				    		this.links = res.data[0].link;
				    		this.tinymceHtml = res.data[0].content;
				    		$('#noticeBtnRow').hide();
				    		$('#noticeModal').modal('show');
				    	}
				    	else{
				    		alert(res.error_msg);
				    	}
				    }.bind(this)
		  		});
			},
			editNotice:function (num) {
				var param = {
					announcementID:num
				}
				$.ajax({
		  			type:'post',
		  			url:getService()+"/findAnnouncement",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == '0') {
				    		this.announcementTopic = res.data[0].announcementTopic;
				    		this.links = res.data[0].link;
				    		this.tinymceHtml = res.data[0].content;
				    		this.announcementID = res.data[0].announcementID;
				    		$('#noticeBtnRow').show();
				    		$('#noticeModal').modal('show');
				    		this.flag = false;
				    	}
				    	else{
				    		alert(res.error_msg);
				    	}
				    }.bind(this)
		  		});
			},
			deleteNotice:function (num) {
				var param = {
					announcementID:num
				}
				$.ajax({
		  			type:'post',
		  			url:getService()+"/delAnnouncement",
		  			dataType: "json",
		  			data:JSON.stringify(param),
		  			async:true,
					headers: {'Content-Type': 'application/json'},
		  			xhrFields: {
				        withCredentials: true
				    },
				    crossDomain: true,
				    success:function (res) {
				    	if (res.error_code == '0') {
				    		window.location.reload();
				    	}
				    	else{
				    		alert(res.error_msg);
				    	}
				    }.bind(this)
		  		});
			}
		}
	}
</script>

<style>
	@import url("../../assets/css/PCstyle.css");
	.paginate ul li{list-style: none;float: left;padding: 3px 6px;width: auto;border: none;}
	.paginate ul li a:hover{color: #ff8500;}
	.paginate ul .active a{color: #ff8500;}
	.paginate ul .active{border: none;}
	.nave li{list-style: none;width: 100px;border: 1px solid #666;border-bottom: none;border-top-left-radius: 5px;border-top-right-radius: 5px;line-height: 30px;color: #666;font-size: 12px;float: left;}
	.nave .active{border: 1px solid #33b8ff;border-bottom: none;border-top-left-radius: 5px;border-top-right-radius: 5px;line-height: 30px;color: #33b8ff;}
	table th,td{text-align: center;white-space: nowrap;/*控制单行显示*/
overflow: hidden;/*超出隐藏*/
text-overflow: ellipsis;/*隐藏的字符用省略号表示*/}
	table th{background: #33b8ff;color: #fff;}
	
</style>


